import './App.css';
import { useState } from 'react'

function App() {
  const [time, setTime] = useState(Time)

  function Time() {
    return [new Date().getHours().toString().padStart(2, '0'), new Date().getMinutes().toString().padStart(2, '0')]
  }
  
  setInterval(() => {
    setTime(Time)
  }, 1000)

  return (
    <div className="App">
      <video className="liveBgBox" style={{ display: 'block' }} autoPlay loop muted src="https://cdn.jsdelivr.net/gh/MobiusBeta/assets/videos/Live_Wallpaper_2.mp4"></video>
      <img className="bgbox" alt="" src="https://cdn.jsdelivr.net/gh/MobiusBeta/assets/images/BG_A_Default_2.jpg"></img>
      <div className="cover" style={{ opacity: 1 }}></div>
      <div className="Time">
        <h1>{ time[0] }<span> : </span>{ time[1] }</h1>
      </div>
      <input className="Search" type="text" placeholder="Search" autoComplete="off" size="30" />
    </div>
  );
}

export default App;
